.Header {
  background-color: #4b5489;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: var(--container_max-width);
  padding: 0.8rem;
  color: white;
}

.logo {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
}

.cart-btn {
  color: white;
  font-size: 2rem;
}